import API from './api';

const apiAuthClient = {

    async login(requestData) {
        console.log('Login: ', requestData);
        await API.post("login/", requestData)
            .then(response => {
                console.log('Login processed successfully');
                return response.data;
            })
            .catch(error => {
                if (error.response) {
                    console.log(error.response);
                }
                return { status: 401, message: 'Unauthorized' }
            });
    },
};

export default apiAuthClient;