import React, {useEffect, useState} from 'react';
import {useFetching} from "../../../hooks/useFetching";
import {apiGeoClient} from "../../../api/apiGeoClient";
import AppAutocomplete from "../../AppAutocomplete";

const CarForm = () => {

    const [region, setRegion] = useState(null)
    const [regions, setRegions] = useState([])

    const [city, setCity] = useState(null)
    const [cities, setCities] = useState([])

    const [fetchRegions, isRegionsLoading, regionsError] = useFetching(async () => {
        const response = await apiGeoClient.getRegions()
        setRegions(response.data)
    })

    const [fetchCities, isCitiesLoading, citiesError] = useFetching(async () => {
        const response = await apiGeoClient.getCitiesByRegionId(region.id)
        setCities(response.data)
        console.dir(citiesError)
    })

    useEffect(() => {
        fetchRegions()
    }, [])

    useEffect(() => {
        if (region) {
            fetchCities()
        } else {
            setCity(null)
        }
    }, [region])


    return (
        <div>
            <AppAutocomplete
                options={regions}
                value={region}
                setValue={setRegion}
                field={'name'}
            />
            <AppAutocomplete
                options={cities}
                value={city}
                setValue={setCity}
                field={'name'}
            />
        </div>

    );
};


export default CarForm;