import API from './api';

const apiCarClient = {

    async getCar() {
        console.log('Sending get all cars request');
        await API.get("car/")
            .then(response => {
                console.log('Get cars: ', response.data);
                return response.data;
            })
            .catch(error => {
                if (error.response) {
                    console.log(error.response);
                    return error.response;
                }
            });
    },

    async getCarById(carId) {
        console.log('Sending get request for car with id=', carId);
        await API.get("car/" + carId)
            .then(response => {
                console.log('Get car: ', response.data);
                return response.data;
            })
            .catch(error => {
                if (error.response) {
                    console.log(error.response);
                    return error.response;
                }
            });
    },

    async saveCar(requestData) {
        console.log('Sending create car request: ', requestData);
        await API.post("car/", requestData)
            .then(response => {
                console.log('Car was saved');
                return response.data;
            })
            .catch(error => {
                if (error.response) {
                    console.log(error.response);
                    return error.response;
                }
            });
    },

    async updateCar(requestData) {
        console.log('Sending update car request: ', requestData);
       await API.patch("car/" + requestData.id, requestData)
            .then(response => {
                console.log('Car was updated');
                return response.data;
            })
            .catch(error => {
                if (error.response) {
                    console.log(error.response);
                    return error.response;
                }
            })
    },

    async deleteCar(carId) {
        console.log('Sending delete car request for car with id=', carId);
       await API.delete("car/" + carId)
            .then(response => {
                console.log('Car was deleted');
                return response.data;
            })
            .catch(error => {
                if (error.response) {
                    console.log(error.response);
                    return error.response;
                }
            });
    },


};

export default apiCarClient;