import React, {Component} from 'react'
import './styles/App.css';
import {BrowserRouter as Router} from "react-router-dom";
import Context from "./hooks/Context";
import apiCarClient from "./api/apiCarClient";
import apiAuthClient from "./api/apiAuthClient";
import jwt_decode from 'jwt-decode';
import AppRouter from "./router/AppRouter";
import AppNavBar from "./components/navbar/index";
import Logo from "./components/Logo";


export default class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: null,
            cars: []
        };
    }

    async componentDidMount() {
        let user = localStorage.getItem("user");
        const cars = await apiCarClient.getCar();
        user = user ? JSON.parse(user) : null;
        this.setState({user, cars: cars});
    }

    login = async (login, password) => {
        const response = apiAuthClient.login();
        if ((await response).status === 200) {
            const {login} = jwt_decode(response.data.accessToken)
            const user = {
                login,
                token: response.data.accessToken
            }
            this.setState({user});
            localStorage.setItem("user", JSON.stringify(user));
            return true;
        } else {
            return false;
        }
    }

    logout = e => {
        e.preventDefault();
        this.setState({user: null});
        localStorage.removeItem("user");
    };

    addCar = (car, callback) => {
        let cars = this.state.cars.slice();
        cars.push(car);
        this.setState({cars}, () => callback && callback());
    };

    render() {
        return (
            <Context.Provider
                value={{
                    ...this.state,
                    login: this.login,
                    addCar: this.addCar,
                }}
            >
                <Router>
                    <div className="App">
                        <AppNavBar/>
                        <Logo/>
                        <AppRouter/>
                    </div>
                </Router>
            </Context.Provider>
        );
    }
}

