import * as React from 'react';
import {useEffect, useState} from 'react';
import {Card, Container, FormControl, InputLabel, OutlinedInput, TextField} from "@mui/material";
import Box from "@mui/material/Box";
import {LoadingButton} from "@mui/lab";
import SaveIcon from '@mui/icons-material/Save';
import {red} from "@mui/material/colors";

export default function Account() {

    const [personData, setPersonData] = useState({
        email: 'emailUSER@mail.ru',
        firstName: 'Иван',
        lastName: 'Иванов',
        meddleName: 'Иванович',
        phone: '+79008007060',
        region: 'Московская область',
        city: 'Москва',
    })

    const [isLoading, setIsLoading] = useState(false)

    const changeLoading = () => {
        setIsLoading(true)
        setTimeout(() => {
            setIsLoading(false)
        }, 1500)
    }

    useEffect(() => {
        console.dir(personData)
    }, [personData])

    return (
        <Container
            // sx={{background: {xs: 'red', sm: 'green', md: 'black', lg: 'blue'}, p: 0}}
        >
            <Card sx={{width: '100%', p: 2, mt: 4}}>
                <FormControl fullWidth sx={{mt: 2}}>
                    <InputLabel htmlFor="email">Email</InputLabel>
                    <OutlinedInput
                        id="email"
                        value={personData.email}
                        label="email"
                        onChange={e => setPersonData({...personData, email: e.target.value})}
                        disabled
                    />
                </FormControl>
            </Card>
            <Card sx={{width: '100%', p: 2, mt: 4}}>
                <FormControl fullWidth sx={{mt: 2}}>
                    <InputLabel htmlFor="firstName">Фамилия</InputLabel>
                    <OutlinedInput
                        id="firstName"
                        value={personData.firstName}
                        label="Фамилия"
                        onChange={e => setPersonData({...personData, firstName: e.target.value})}
                    />
                </FormControl>
                <FormControl fullWidth sx={{mt: 2}}>
                    <InputLabel htmlFor="lastName">Имя</InputLabel>
                    <OutlinedInput
                        id="lastName"
                        value={personData.lastName}
                        label="Имя"
                        onChange={e => setPersonData({...personData, lastName: e.target.value})}
                    />
                </FormControl>
                <FormControl fullWidth sx={{mt: 2}}>
                    <InputLabel htmlFor="lastName">Отчество</InputLabel>
                    <OutlinedInput
                        id="meddleName"
                        value={personData.meddleName}
                        label="Отчество"
                        onChange={e => setPersonData({...personData, meddleName: e.target.value})}
                    />
                </FormControl>
                <Box
                    fullWidth
                    sx={{
                        mt: 4,
                        display: 'flex',
                        flexDirection: {xs: 'column', sm: 'column', md: 'row'}
                    }}
                >
                    <Box
                        sx={{flex: 1}}
                    >
                        <FormControl
                            sx={{width: '100%'}}
                        >
                            <InputLabel htmlFor="region"
                            >
                                Регион
                            </InputLabel>
                            <OutlinedInput
                                id="region"
                                label="Регион"
                            />
                        </FormControl>
                    </Box>
                    <Box
                        sx={{flex: 1}}
                    >
                        <FormControl
                            sx={{width: '100%'}}
                        >
                            <InputLabel htmlFor="city">Населенный пункт</InputLabel>
                            <OutlinedInput
                                id="city"
                                label="Населенный пункт"
                            />
                        </FormControl>
                    </Box>
                </Box>
                <FormControl fullWidth sx={{mt: 4}}>
                    <InputLabel htmlFor="phone">Номер телефона</InputLabel>
                    <OutlinedInput
                        id="phone"
                        value={personData.phone}
                        label="Номер телефона"
                        onChange={e => setPersonData({...personData, phone: e.target.value})}
                    />
                </FormControl>
                <Box sx={{
                    mt: 4,
                    display: 'flex',
                    width: '100%',
                    justifyContent: 'flex-end'
                }}
                >
                    <LoadingButton
                        onClick={changeLoading}
                        loading={isLoading}
                        loadingPosition="start"
                        startIcon={<SaveIcon/>}
                        variant="contained"
                        size="large"
                        sx={{width: {xs: '100%', md: 150}}}
                    >
                        Сохранить
                    </LoadingButton>
                </Box>
            </Card>
        </Container>
    )
        ;
}