import React from 'react';
import {Route, Routes} from "react-router-dom";
import CarList from "../pages/CarList";
import {
    ABOUT_ROUTE,
    ACCOUNT_ROUTE,
    CAR_LIST_ROUTE,
    CONTACTS_ROUTE,
    LOGIN_ROUTE,
    TERMS_ROUTE,
    USER_CARS_ROUTE
} from "../constants/pagePaths";
import About from "../pages/About";
import Contacts from "../pages/Contacts";
import Login from "../pages/Login";
import NotFound from "../pages/NotFound";
import Account from "../pages/Account";
import Terms from "../pages/Terms";
import UserCars from "../pages/UserCars";

const AppRouter = () => {
    let isAuth = true
    return (
        <Routes>
            {isAuth
                ? privateRoutes().map(route =>
                    <Route
                        key={route.path}
                        path={route.path}
                        element={route.element}
                    />
                )
                : publicRoutes().map(route =>
                    <Route
                        key={route.path}
                        path={route.path}
                        element={route.element}
                    />
                )
            }
        </Routes>
    );
};

const publicRoutes = () => {
    return [
        {path: '/', element: <CarList/>},
        {path: CAR_LIST_ROUTE, element: <CarList/>},
        {path: ABOUT_ROUTE, element: <About/>},
        {path: TERMS_ROUTE, element: <Terms/>},
        {path: CONTACTS_ROUTE, element: <Contacts/>},
        {path: LOGIN_ROUTE, element: <Login/>},
        {path: ACCOUNT_ROUTE, element: <Login/>},
        {path: USER_CARS_ROUTE, element: <Login/>},
        {path: '/*', element: <NotFound/>}
    ]
}

const privateRoutes = () => {
    return [
        {path: '/', element: <CarList/>},
        {path: CAR_LIST_ROUTE, element: <CarList/>},
        {path: ABOUT_ROUTE, element: <About/>},
        {path: TERMS_ROUTE, element: <Terms/>},
        {path: CONTACTS_ROUTE, element: <Contacts/>},
        {path: LOGIN_ROUTE, element: <Login/>},
        {path: ACCOUNT_ROUTE, element: <Account/>},
        {path: USER_CARS_ROUTE, element: <UserCars/>},
        {path: '/*', element: <NotFound/>}
    ]
}

export default AppRouter;