import React, {useState} from 'react';
import AppBar from "@mui/material/AppBar";
import {Button, Container, IconButton, Menu, MenuItem, Toolbar, Typography} from "@mui/material";
import Box from "@mui/material/Box";
import MenuIcon from '@mui/icons-material/Menu';
import AccountCircle from '@mui/icons-material/AccountCircle';
import {useNavigate} from "react-router-dom";
import {publicPages, privatePages} from "./navbar";
import cl from "./index.module.css";


const AppNavBar = () => {
    let isAuth = true

    const navigate = useNavigate()
    const [anchorElNav, setAnchorElNav] = useState(null);
    const [anchorElUser, setAnchorElUser] = useState(null);

    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget);
    };
    const handleOpenUserMenu = (event) => {
        setAnchorElUser(event.currentTarget);
    };

    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
    };

    const handleCloseUserMenu = () => {
        setAnchorElUser(null);
    };

    const changePage = (path, handlerCloseMenu) => {
        navigate(path)
        handlerCloseMenu()
    }

    return (
        <Box
            className={cl.navBar}
            sx={{backgroundColor: 'secondary.main'}}
            component='Navbar'
        >
            <Box className={cl.containerNavMenu}>
                <IconButton
                    size="large"
                    aria-label="account of current user"
                    aria-controls="menu-appbar"
                    aria-haspopup="true"
                    onClick={handleOpenNavMenu}
                    color="inherit"
                >
                    <MenuIcon/>
                </IconButton>
                <Menu
                    id="menu-appbar"
                    anchorEl={anchorElNav}
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                    }}
                    keepMounted
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'left',
                    }}
                    open={Boolean(anchorElNav)}
                    onClose={handleCloseNavMenu}
                    sx={{
                        display: {xs: 'block', md: 'none'},
                    }}
                >
                    {publicPages.map(page =>
                        <MenuItem
                            key={page.path}
                            onClick={() => changePage(page.path, handleCloseNavMenu)}
                        >
                            <Typography
                                textAlign="center"
                            >
                                {page.title}
                            </Typography>
                        </MenuItem>
                    )}
                </Menu>
            </Box>
            <Box className={cl.containerNavItems}>
                {publicPages.map(page =>
                    <Button
                        className={cl.navItem}
                        key={page.path}
                        onClick={() => changePage(page.path, handleCloseNavMenu)}
                        sx={{
                            backgroundColor: page.color,
                            color: 'text.primary',
                            marginRight: '2px'
                        }}
                    >
                        {page.title}
                    </Button>
                )}
            </Box>
            <Box className={cl.containerUserMenu}>
                <IconButton
                    size="large"
                    aria-label="account of current user"
                    aria-controls="menu-appbar"
                    aria-haspopup="true"
                    onClick={handleOpenUserMenu}
                    color="inherit"
                    sx={{display: {xs: 'block', sm: 'block', md: 'none'}}}
                >
                    <AccountCircle/>
                </IconButton>
                <Button
                    className={[cl.navItem, cl.accountBtn].join(' ')}
                    sx={{
                        backgroundColor: 'primary.dark',
                        color: 'text.primary',
                        display: {xs: 'none', sm: 'none', md: 'block'}
                    }}
                    onClick={handleOpenUserMenu}
                >
                    Личный кабинет
                </Button>
                <Menu
                    sx={{mt: '45px',}}
                    id="menu-appbar"
                    anchorEl={anchorElUser}
                    anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                    keepMounted
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}

                    open={Boolean(anchorElUser)}
                    onClose={handleCloseUserMenu}
                >
                    {isAuth
                        ? <Box>
                            {privatePages.map(page =>
                                <MenuItem
                                    key={page.path}
                                    onClick={() => changePage(page.path, handleCloseUserMenu)}
                                >
                                    <Typography textAlign="center">{page.title}</Typography>
                                </MenuItem>
                            )}
                        </Box>
                        : <Box>
                            <MenuItem
                                onClick={handleCloseUserMenu}
                            >
                                <Typography textAlign="center">Вход</Typography>
                            </MenuItem>
                        </Box>
                    }
                </Menu>
            </Box>
        </Box>
    )
        ;
};

export default AppNavBar;