import React from "react";
import {Card, CardContent, CardMedia, Grid, Typography} from "@mui/material";
import {Link} from "react-router-dom";
import {CAR_LIST_ROUTE} from "../constants/pagePaths";
import Box from "@mui/material/Box";


const CarItem =(car) =>{
    const {loading = false} = car;

    return (
        <Box sx={{m: 2}}>
            <Link
                to={CAR_LIST_ROUTE + "/" + car.id}
                style={{textDecoration: 'none'}}
            >
                <Card sx={{width: 1100, backgroundColor: 'card.main', border: 1, borderRadius: 0}}>
                    <Box sx={{display: 'flex', flexDirection: 'row'}}>
                        <Box sx={{display: 'inline', width: 300, m: 2}}>
                            <CardMedia sx={{border: 1, borderRadius: 0, backgroundColor: 'card.img'}}
                                       component="img"
                                       height="150"
                                       image={car.img}
                                       alt={car.alt}
                            />
                        </Box>
                        <Box sx={{flexGrow: 1,}}>
                            <CardContent>
                                <Typography>
                                    <Grid
                                        container
                                        spacing={2}
                                    >
                                        <Grid container xs={12} alignItems="left">
                                            <Box
                                                sx={{
                                                    mt: 2,
                                                    fontSize: 26,
                                                    fontWeight: 700
                                                }}
                                            >
                                                {car.model}
                                            </Box>
                                        </Grid>
                                        <Grid container xs={12} justifyContent="flex-end">
                                            <Box
                                                sx={{
                                                    fontSize: 20,
                                                    fontWeight: 500
                                                }}
                                            >
                                                {car.price + " руб/сутки"}
                                            </Box>

                                        </Grid>
                                        <Grid container xs={12}>
                                        </Grid>
                                        <Grid container xs={4}>
                                            <Box sx={{
                                                fontSize: 15,
                                                fontWeight: 500,
                                                mt: 5,
                                            }}
                                            >
                                                {"Мощность: " + car.power + "л.с"}
                                            </Box>
                                        </Grid>
                                        <Grid container xs={4}>
                                            <Box
                                                sx={{
                                                    fontSize: 15,
                                                    fontWeight: 500,
                                                    mt: 5,
                                                }}
                                            >
                                                {"Трансмиссия: " + car.transmission}
                                            </Box>
                                        </Grid>
                                    </Grid>
                                </Typography>
                            </CardContent>
                        </Box>
                    </Box>
                </Card>
            </Link>
        </Box>
    );
}

export default CarItem

